<template>
  <div class="auth-page">
    <div class="auth-card">
      <div class="auth-header">
        <router-link to="/">
          <img src="@/assets/logo.svg" alt="AI2Cloth Logo" class="logo-img">
        </router-link>
        <h2>Ê¨¢ËøéÂõûÊù•</h2>
        <p>ÁôªÂΩï‰ª•ÁªßÁª≠‰Ω†ÁöÑÊó∂Â∞ö‰πãÊóÖ</p>
      </div>
      <form @submit.prevent="handleLogin" class="auth-form">
        <div class="form-item" :class="{ error: errors.email }">
          <div class="input-wrapper">
            <span class="input-icon">üìß</span>
            <input 
              type="email" 
              v-model="loginForm.email" 
              placeholder="ÈÇÆÁÆ±"
              class="form-input"
              @blur="validateEmail"
            >
          </div>
          <div v-if="errors.email" class="error-message">{{ errors.email }}</div>
        </div>
        
        <div class="form-item" :class="{ error: errors.password }">
          <div class="input-wrapper">
            <span class="input-icon">üîí</span>
            <input 
              :type="showPassword ? 'text' : 'password'" 
              v-model="loginForm.password" 
              placeholder="ÂØÜÁ†Å"
              class="form-input"
              @blur="validatePassword"
            >
            <button 
              type="button" 
              class="password-toggle"
              @click="showPassword = !showPassword"
            >
              {{ showPassword ? 'üôà' : 'üëÅÔ∏è' }}
            </button>
          </div>
          <div v-if="errors.password" class="error-message">{{ errors.password }}</div>
        </div>
        
        <div class="form-item">
          <button 
            type="submit" 
            class="auth-button" 
            :disabled="loading"
            :class="{ loading: loading }"
          >
            <span v-if="loading" class="loading-spinner"></span>
            {{ loading ? 'ÁôªÂΩï‰∏≠...' : 'Á´ãÂç≥ÁôªÂΩï' }}
          </button>
        </div>
      </form>
      <div class="auth-footer">
        ËøòÊ≤°ÊúâË¥¶Êà∑Ôºü <router-link to="/register">Á´ãÂç≥Ê≥®ÂÜå</router-link>
      </div>
    </div>
  </div>
</template>

<script>
/**
 * @description Áî®Êà∑ÁôªÂΩïÈ°µÈù¢
 */
import { mapActions } from 'vuex';

export default {
  name: 'Login',
  data() {
    return {
      loading: false,
      showPassword: false,
      loginForm: {
        email: '',
        password: ''
      },
      errors: {
        email: '',
        password: ''
      }
    };
  },
  methods: {
    ...mapActions('auth', ['login']),
    
    validateEmail() {
      this.errors.email = '';
      if (!this.loginForm.email) {
        this.errors.email = 'ËØ∑ËæìÂÖ•ÈÇÆÁÆ±';
      } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.loginForm.email)) {
        this.errors.email = 'ËØ∑ËæìÂÖ•Ê≠£Á°ÆÁöÑÈÇÆÁÆ±Ê†ºÂºè';
      }
    },
    
    validatePassword() {
      this.errors.password = '';
      if (!this.loginForm.password) {
        this.errors.password = 'ËØ∑ËæìÂÖ•ÂØÜÁ†Å';
      }
    },
    
    validateForm() {
      this.validateEmail();
      this.validatePassword();
      return !this.errors.email && !this.errors.password;
    },
    
    async handleLogin() {
      if (!this.validateForm()) {
        return;
      }
      
      this.loading = true;
      try {
        await this.login(this.loginForm);
        const redirect = this.$route.query.redirect || '/dashboard';
        this.$router.push(redirect);
        this.showMessage('ÁôªÂΩïÊàêÂäüÔºÅ', 'success');
      } catch (error) {
        const detail = error.response?.data?.detail || 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊÇ®ÁöÑÂá≠ÊçÆ„ÄÇ';
        this.showMessage(detail, 'error');
      } finally {
        this.loading = false;
      }
    },
    
    showMessage(message, type = 'info') {
      const messageEl = document.createElement('div');
      messageEl.className = `custom-message custom-message-${type}`;
      messageEl.textContent = message;
      
      Object.assign(messageEl.style, {
        position: 'fixed',
        top: '20px',
        left: '50%',
        transform: 'translateX(-50%)',
        padding: '12px 20px',
        borderRadius: '6px',
        color: 'white',
        fontSize: '14px',
        zIndex: '9999',
        transition: 'all 0.3s ease'
      });
      
      if (type === 'error') {
        messageEl.style.backgroundColor = '#F56C6C';
      } else if (type === 'success') {
        messageEl.style.backgroundColor = '#67C23A';
      } else {
        messageEl.style.backgroundColor = '#409EFF';
      }
      
      document.body.appendChild(messageEl);
      
      setTimeout(() => {
        messageEl.style.opacity = '0';
        setTimeout(() => {
          document.body.removeChild(messageEl);
        }, 300);
      }, 3000);
    }
  }
};
</script>

<style lang="scss" scoped>
@import '../styles/auth.scss';

// ÂéüÁîüË°®ÂçïÊ†∑Âºè
.auth-form {
  width: 100%;
}

.form-item {
  margin-bottom: 20px;
  
  &.error .input-wrapper {
    border-color: #F56C6C;
  }
}

.input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  padding: 0 15px;
  transition: all 0.3s ease;
  
  &:focus-within {
    border-color: #409EFF;
    box-shadow: 0 0 0 2px rgba(64, 158, 255, 0.2);
  }
}

.input-icon {
  margin-right: 10px;
  font-size: 16px;
  color: #9aa4b2;
}

.form-input {
  flex: 1;
  background: transparent;
  border: none;
  outline: none;
  color: #e6e6e6;
  font-size: 16px;
  padding: 15px 0;
  
  &::placeholder {
    color: #9aa4b2;
  }
}

.password-toggle {
  background: none;
  border: none;
  color: #9aa4b2;
  cursor: pointer;
  padding: 5px;
  margin-left: 10px;
  font-size: 16px;
  transition: color 0.3s ease;
  
  &:hover {
    color: #409EFF;
  }
}

.error-message {
  color: #F56C6C;
  font-size: 12px;
  margin-top: 5px;
  margin-left: 5px;
}

.auth-button {
  width: 100%;
  background: linear-gradient(135deg, #409EFF, #337ecc);
  color: white;
  border: none;
  padding: 15px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  
  &:hover:not(:disabled) {
    background: linear-gradient(135deg, #337ecc, #2b6cb0);
    transform: translateY(-1px);
    box-shadow: 0 4px 12px rgba(64, 158, 255, 0.3);
  }
  
  &:disabled {
    opacity: 0.6;
    cursor: not-allowed;
  }
  
  .loading-spinner {
    width: 16px;
    height: 16px;
    border: 2px solid transparent;
    border-top: 2px solid white;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
</style>
